/* REACT TRANSITION GROUP STYLES */

.fadeIn-appear {
  opacity: 0;
}
.fadeIn-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms;
  transition: opacity 500ms;
}
.fadeIn-enter {
  opacity: 0;
}
.fadeIn-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}
.fadeIn-exit {
  opacity: 1;
}
.fadeIn-exit-active {
  opacity: 0;
  -webkit-transition: opacity 500ms;
  transition: opacity 500ms;
}


.pop-appear {
  opacity: 0;
  -webkit-transform: scale(.98);
          transform: scale(.98);
}
.pop-appear-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 300ms ease-in;
  transition: all 300ms ease-in;
}

.pop-enter {
  opacity: 0;
  -webkit-transform: scale(.98);
          transform: scale(.98);
}
.pop-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 300ms ease-in;
  transition: all 300ms ease-in;
}
.pop-leave {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.pop-leave-active {
  opacity: 0;
  -webkit-transform: scale(.98);
          transform: scale(.98);
  -webkit-transition: all 300ms;
  transition: all 300ms;
}
.pop-exit {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.pop-exit-active {
  opacity: 0;
  -webkit-transform: scale(.98);
          transform: scale(.98);
  -webkit-transition: all 300ms;
  transition: all 300ms;
}
